import React, { Component } from 'react';
import { FaYoutube, FaLinkedin, FaTwitter, FaFacebook, FaInstagramSquare } from 'react-icons/fa';

// Style
import '../style/footer.css';

class Footer extends Component {
    render() {
        return (
            <footer>

                <h1>Logo</h1>

                <div id="footer-socials">
                    <FaTwitter size={30} color="#14203E" />
                    <FaFacebook size={30} color="#14203E" />
                    <FaInstagramSquare size={30} color="#14203E" />
                    <FaYoutube size={30} color="#14203E" />
                    <FaLinkedin size={30} color="#14203E" />
                </div>

            </footer>
        );
    }
}

export default Footer;