import React, { Component } from 'react';
import Header from '../components/Header';

// Style
import '../style/home.css';

// Assets
import post1 from '../assets/img/blog-post-1.png';
import post2 from '../assets/img/blog-post-2.png';
import Footer from '../components/Footer';

class Home extends Component {
    render() {
        return (
            <div id="Home">
                
                <Header />

                {/* Hero Banner */}
                <div id="hero-banner">

                    <div id="hero-banner-text">

                        <h1>Une technique moderne pour la création de votre site web</h1>

                        <p>Permet aux designers de construire leur site web professionnel, personnalisé de façon visuelle sans code.</p>

                        <a class="btn special">Essaie gratuit</a>

                    </div>

                </div>

                {/* Blog */}
                <div id="blog-header">
                
                    <hr />

                    <h3>On partage nos pensés sur le design</h3>

                    <hr />

                </div>

                <div id="blog-posts">

                    <div class="blog-posts-container">
                        <img src={post1} alt="Blog image 1" />
                        <h3>Le redesign de mon site a augmenté mes ventes de 400%</h3>
                        <p>1 Juil. 2021</p>
                    </div>

                    <div class="blog-posts-container">
                        <img src={post2} alt="Blog image 1" />
                        <h3>Transfert de conception: Ajuster le design à la conception</h3>
                        <p>1 Juil. 2021</p>
                    </div>

                    <div class="blog-posts-container">
                        <img src={post1} alt="Blog image 1" />
                        <h3>Une liste complète des intéractions homme/machine</h3>
                        <p>1 Juil. 2021</p>
                    </div>

                </div>

                {/* Footer */}
                <Footer />

            </div>
        );
    }
}

export default Home;